export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <div\n        class=\"is-bordered has-padding-md is-rounded\"\n        style=\"position: relative;height: 400px;\"\n      >\n        Some content\n        <VLoadingBlend\n          :loading=\"loading\"\n        />\n      </div>\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"loading\"\n          label=\"Loading\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobBoolean\n} from '@/docs/components';\nimport {\n  VLoadingBlend\n} from '@/components';\n\nexport default {\n  name: 'LoadingBlendExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobBoolean,\n    VLoadingBlend\n  },\n  data () {\n    return {\n      loading: true\n    };\n  },\n  mounted () {\n    setTimeout(() => {\n      this.loading = false;\n    }, 2000);\n  }\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";